import React, { useRef } from 'react';
import { useState, useEffect } from 'react';
import { Alert, Box, Button, Container, Dialog, DialogContent, DialogTitle, List, Stack, TextField, Typography } from '@mui/material';
import { NavigateFunction, useNavigate } from 'react-router-dom';
import './hover.css';


interface ApiData {
    canvases: []
    error: string
    fetched: boolean
}

interface Canvas {
    id: number
    name: string
    createdAt: Date
}

interface FetchSettings {
    method: string
    headers?: any
    body?: string
}

const FrontPage: React.FC = (): React.ReactElement => {

    const [apiData, setApiData] = useState<ApiData>({
        canvases: [],
        error: "",
        fetched: false
    });
    const [readyToMove, setReadyToMove] = useState<boolean>(false);
    const [dialogOpen, setDialogOpen] = useState<boolean>(false);
    const [selectedCanvas, setSelectedCanvas] = useState<Canvas | undefined>(undefined);

    const navigate: NavigateFunction = useNavigate();
    const formRef: any = useRef<HTMLFormElement>();

    const apiCall = async (metodi?: string, canvasName?: string): Promise<void> => {

        let url = `/api/canvases`;

        let settings: FetchSettings = {
            method: metodi || "GET",
        };

        if (metodi === "POST") {
            console.log(canvasName);
            settings = {
                ...settings,
                headers: {
                    ...settings.headers,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ canvasName })
            }

        }

        try {

            const yhteys = await fetch(url, settings);

            if (yhteys.status === 200) {

                setApiData({
                    ...apiData,
                    canvases: await yhteys.json(),
                    fetched: true
                });

            } else {

                let virheteksti: string = "Unexpected error";

                setApiData({
                    ...apiData,
                    error: virheteksti,
                    fetched: true
                });

            }

        } catch (e: any) {

            setApiData({
                ...apiData,
                error: "Can't connect to server",
                fetched: true
            });
        }
    }

    const handleOpen = (canvas: Canvas) => {
        setSelectedCanvas(canvas);
        setDialogOpen(true);
    }

    const handleClose = () => {
        setDialogOpen(false);
        setSelectedCanvas(undefined);
    }

    const openCanvas = (e: React.FormEvent) => {
        e.preventDefault();

        if (selectedCanvas) {
            navigate(`/canvases/${selectedCanvas.id}`, { state: String(formRef.current?.username.value) });
        } else {
            setReadyToMove(true);
            apiCall("POST", String(formRef.current?.canvasname.value));
        }
    }

    const move = () => {
        let asd = Math.max(...apiData.canvases.map((el: Canvas) => el.id))
        navigate(`/canvases/${asd}`, { state: String(formRef.current?.username.value) });
    }

    useEffect(() => {
        apiCall();
    }, []);

    useEffect(() => {
        if (readyToMove) {
            move();
        }
        setReadyToMove(false);
    }, [apiData]);


    return (
        <Container>
            {(Boolean(apiData.error)) ? <Alert severity='error'>{apiData.error}</Alert>
                : <Box>
                    <Button
                        fullWidth
                        variant='contained'
                        onClick={() => setDialogOpen(true)}
                    >New canvas

                    </Button>
                    <List>
                        {

                            apiData.canvases.map((canvas: Canvas, idx: number) => {
                                return <Box key={idx}
                                    className='hoverelement'
                                    marginBottom="5px"
                                    borderRadius="20px"
                                    padding="15px"
                                    bgcolor="#f0f0f0"
                                    display={'flex'}
                                    onClick={() => handleOpen(canvas)}
                                >
                                    <Typography width='50%'>{canvas.name}</Typography>
                                    <Typography>{canvas.createdAt.toString()}</Typography>
                                </Box>
                            })
                        }
                    </List>
                </Box>
            }
            <Dialog

                maxWidth="lg"
                fullWidth={true}
                open={dialogOpen}
                onClose={handleClose}
            >
                <DialogTitle>
                    {(selectedCanvas)
                        ? "Selected canvas: " + selectedCanvas.name
                        : "New canvas"
                    }
                </DialogTitle>
                <DialogContent style={{ paddingTop: 10 }}>
                    <Stack
                        spacing={1}
                        component="form"
                        onSubmit={openCanvas}
                        ref={formRef}
                    >
                        <TextField
                            name="username"
                            label="Username"
                            fullWidth
                            variant="outlined"
                        />
                        <TextField
                            name="canvasname"
                            label={"Canvas name"}
                            fullWidth
                            variant="outlined"
                            style={(Boolean(selectedCanvas) ? { display: "none" } : {})}
                        />

                        <Button
                            variant="contained"
                            type="submit"
                        >Continue</Button>

                        <Button
                            variant="outlined"
                            onClick={handleClose}
                        >Back</Button>

                    </Stack>
                </DialogContent>

            </Dialog>

        </Container>
    );
}

export default FrontPage;
