import React from 'react';
import { useState } from 'react';
import DrawArea from './DrawArea';
import RectangleOutlinedIcon from '@mui/icons-material/RectangleOutlined';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import HorizontalRuleOutlinedIcon from '@mui/icons-material/HorizontalRuleOutlined';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup/ToggleButtonGroup';
import ToggleButton from '@mui/material/ToggleButton/ToggleButton';
import Box from '@mui/material/Box/Box';
import Container from '@mui/material/Container/Container';
import { useLocation, useParams } from 'react-router-dom';
import { InputLabel, Slider } from '@mui/material';
import { CompactPicker } from 'react-color';

const DrawBoard: React.FC = (): React.ReactElement => {
	const [fillColor, setFillColor] = useState<string>("black");
	const [strokeColor, setStrokeColor] = useState<string>("black");
	const [opacity, setOpacity] = useState<number>(100);
	const [width, setWidth] = useState<number>(1);
	const [shape, setShape] = React.useState('rectangle');

	const { id } = useParams();
	const { state } = useLocation();

	const handleChange = (
		event: React.MouseEvent<HTMLElement>,
		newShape: string,
	) => {
		setShape(newShape);
	};

	const handleOpacitySlider = (event: Event, newValue: number | number[]) => {
		setOpacity(newValue as number);
	};

	const handleWidthSlider = (event: Event, newValue: number | number[]) => {
		setWidth(newValue as number);
	};

	const buttons = [
		<ToggleButton value={"rectangle"} key="rectangle"><RectangleOutlinedIcon /></ToggleButton>,
		<ToggleButton value={"ellipse"} key="ellipse"><CircleOutlinedIcon /></ToggleButton>,
		<ToggleButton value={"line"} key="line"><HorizontalRuleOutlinedIcon /></ToggleButton>
	];

	const control = {
		value: shape,
		onChange: handleChange,
		exclusive: true
	};


	return (
		<Container maxWidth={'lg'}>
			<Box>
				<DrawArea
					canvasId={Number(id)}
					fillColor={fillColor}
					strokeColor={strokeColor}
					opacity={opacity * 0.01}
					width={width}
					shapeType={shape}
					currentUser={state}></DrawArea>
				<Box display='flex'>
					<ToggleButtonGroup {...control}>
						{buttons}
					</ToggleButtonGroup>
				</Box>
				<Box display={'flex'}>
					<Box>
						<InputLabel>Stroke color</InputLabel>
						<CompactPicker onChange={(e) => setStrokeColor(e.hex)}></CompactPicker>
					</Box>
					<Box>
						<InputLabel>Fill color</InputLabel>
						<CompactPicker onChange={(e) => setFillColor(e.hex)}></CompactPicker>
					</Box>
				</Box>

				<Box maxWidth={500}>
					<InputLabel>Width</InputLabel>
					<Slider min={1} max={10} defaultValue={1} onChange={handleWidthSlider}></Slider>
					<InputLabel>Opacity</InputLabel>
					<Slider min={0} max={100} defaultValue={100} onChange={handleOpacitySlider}></Slider>
				</Box>
			</Box>
		</Container>
	);
}

export default DrawBoard;
