import React from 'react';
import Typography from '@mui/material/Typography';
import { Box, IconButton } from '@mui/material';
import { ShapeData } from './interfaces';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

interface Props {
	shapeData: ShapeData | undefined
	setShape: Function
	apiCall: Function
}

const ShapeInfo: React.FC<Props> = (props: Props): React.ReactElement => {

	const deleteShape = () => {
		props.apiCall("DELETE", props.shapeData);
		props.setShape(undefined);
	}

	return (
		<Box margin="20px">
			<Typography variant='h4'>Shape info</Typography>
			{
				(props.shapeData)
					? <>
						<Typography>Created: {props.shapeData?.createdAt?.toString()}</Typography>
						<Typography>Created by: {props.shapeData?.username}</Typography>
						<Typography>Type: {props.shapeData?.shape.type}</Typography>
						<Typography>Opacity: {props.shapeData?.shape.opacity}</Typography>
						<Typography>Width: {props.shapeData?.shape.width}</Typography>
						<IconButton onClick={deleteShape}><DeleteForeverIcon color='error' /></IconButton>
					</>
					:
					<Typography>No shape selected</Typography>
			}
		</Box>
	);
}

export default ShapeInfo;
