import React from 'react';
import { useState, useEffect } from 'react';
import { Alert, Box, Button } from '@mui/material';
import ShapeInfo from './ShapeInfo';
import { ShapeData, Shape } from './interfaces';
import { NavigateFunction, useNavigate } from 'react-router-dom';


interface ApiData {
    error: string
    fetched: boolean
}

interface FetchSettings {
    method: string
    headers?: any
    body?: string
}

interface Props {
    canvasId: number
    currentUser: string
    shapeType: string
    fillColor: string
    strokeColor: string
    opacity: number
    width: number
}

interface Position {
    x: number
    y: number
}

const DrawArea: React.FC<Props> = (props: Props): React.ReactElement => {
    const [apiData, setApiData] = useState<ApiData>({ error: "", fetched: false });
    const [shapes, setShapes] = useState<ShapeData[]>([]);
    const [startingPos, setStartingPos] = useState<Position>({ x: 0, y: 0 });
    const [mouseDown, setMouseDown] = useState<Boolean>(false);
    const [drawing, setDrawing] = useState<Boolean>(false);
    const [ghostShape, setGhostShape] = useState<Shape | undefined>(undefined);
    const [selectedShapeData, setSelectedShapeData] = useState<ShapeData | undefined>(undefined);

    const navigate: NavigateFunction = useNavigate();

    const apiCall = async (method?: string, shapeData?: ShapeData): Promise<void> => {

        setApiData({
            ...apiData,
            fetched: false
        });

        let url = `/api/canvases/${props.canvasId}/shapes`;

        let settings: FetchSettings = {
            method: method || "GET"
        };

        if (method === "POST") {

            settings = {
                ...settings,
                headers: {
                    ...settings.headers,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(shapeData)
            }
        }

        if (method === "DELETE") {

            settings = {
                ...settings,
                headers: {
                    ...settings.headers,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(shapeData)
            }
        }

        try {

            const connection = await fetch(url, settings);

            if (connection.status === 200) {

                setApiData({
                    ...apiData,
                    fetched: true
                });
                const newShapes: ShapeData[] = await connection.json();
                setShapes(newShapes);
            } else {

                let errortext: string = "";

                switch (connection.status) {

                    case 400: errortext = "Error with request"; break;
                    default: errortext = "Unexpected error"; break;

                }

                setApiData({
                    ...apiData,
                    error: errortext,
                    fetched: true
                });
            }

        } catch (e: any) {

            setApiData({
                ...apiData,
                error: "Couldn't connect to server",
                fetched: true
            });
        }
    }

    useEffect(() => {
        apiCall();
    }, []);

    const handleMouseDown = (e: React.MouseEvent<SVGSVGElement>) => {
        setStartingPos({
            x: e.clientX - e.currentTarget.getBoundingClientRect().x,
            y: e.clientY - e.currentTarget.getBoundingClientRect().y
        });
        setMouseDown(true);
    }

    const handleMouseMove = (e: React.MouseEvent<SVGSVGElement>): void => {
        if (mouseDown) {
            setDrawing(true); // only draw if mouse has moved
            setGhostShape(draw(e));
        }
    }

    const handleMouseUp = (e: React.MouseEvent<SVGSVGElement>): void => {
        setMouseDown(false);
        if (drawing) {
            setDrawing(false);
            let shape = draw(e);
            setGhostShape(undefined); // clear ghostshape

            let newShapeData: ShapeData = {
                username: props.currentUser,
                shape: shape
            }
            apiCall("POST", newShapeData);
        }
    }

    const handleMouseClick = (shapeData: ShapeData): void => {
        if (selectedShapeData?.id === shapeData.id) {
            setSelectedShapeData(undefined);
        } else {
            setSelectedShapeData(shapeData);
        }
    }

    const draw = (e: React.MouseEvent<SVGSVGElement>): Shape => {

        let shape: Shape = {
            type: props.shapeType,
            fillColor: props.fillColor,
            strokeColor: props.strokeColor,
            opacity: props.opacity,
            width: props.width,
            x1: startingPos.x,
            y1: startingPos.y,
            x2: e.clientX - e.currentTarget.getBoundingClientRect().x,
            y2: e.clientY - e.currentTarget.getBoundingClientRect().y
        };
        
        return shape;
    }

    const drawShape = (shape: Shape) => {
        let width = Math.abs(shape.x1 - shape.x2);
        let height = Math.abs(shape.y1 - shape.y2);

        if (shape.type === "rectangle") {
            return <rect
                fillOpacity={shape.opacity}
                fill={shape.fillColor}
                stroke={shape.strokeColor}
                x={Math.min(shape.x1, shape.x2)} // start drawing from top left
                y={Math.min(shape.y1, shape.y2)}
                width={width}
                height={height}
                strokeWidth={shape.width}
            ></rect>
        } else if (shape.type === "ellipse") {
            return <ellipse
                fillOpacity={shape.opacity}
                fill={shape.fillColor}
                stroke={shape.strokeColor}
                cx={Math.min(shape.x1, shape.x2) + (width / 2)}
                cy={Math.min(shape.y1, shape.y2) + (height / 2)}
                rx={width / 2}
                ry={height / 2}
                strokeWidth={shape.width}
            ></ellipse>
        } else if (shape.type === "line") {
            return <line
                stroke={shape.strokeColor}
                x1={shape.x1}
                y1={shape.y1}
                x2={shape.x2}
                y2={shape.y2}
                strokeWidth={shape.width}
            ></line>
        }
    }


    return (
        <>
            {(Boolean(apiData.error) ? <Alert severity='error'>{apiData.error}</Alert> : <></>)}

            <Box style={{ display: "flex" }}>
                <svg style={{ backgroundColor: "gray" }} width={500} height={500} onMouseMove={handleMouseMove} onMouseDown={handleMouseDown} onMouseUp={handleMouseUp}>
                    {(ghostShape) ? drawShape(ghostShape) : <></>}
                    {
                        shapes.map((shapeData: ShapeData, idx: number) => {
                            return (
                                <svg
                                    style={selectedShapeData?.id === shapeData.id ? { outline: 'solid', outlineColor: 'red' } : { outline: 'none' }}
                                    key={idx}
                                    onClick={() => handleMouseClick(shapeData)}>
                                    {drawShape(shapeData.shape)}
                                </svg>
                            );
                        })
                    }
                </svg>
                <Box>
                    <Button variant='contained' onClick={() => apiCall()}>REFRESH</Button>
                    <Button color='inherit' variant='contained' onClick={() => navigate("/")}>BACK</Button>
                    <ShapeInfo setShape={setSelectedShapeData} shapeData={selectedShapeData} apiCall={apiCall}></ShapeInfo>
                </Box>
            </Box>
        </>
    );
}

export default DrawArea;
